.index {
	&-banner {
		height: 840px;
		background-image: url(../images/banner.png);
		background-size: cover;

		@include tablet() {
			background-image: url(../images/banner-sm.png);
		}

		@include mobile() {
			background-position: left;
			height: 394px;
		}

		&-text {
			height: 100%;
			color: $color-secondary;
			text-align: right;

			&-1 {
				font-size: 36px;

				@include tablet() {
					font-size: 20px;
				}

				@include mobile() {
					font-size: 16px;
					line-height: 1.875;
				}
			}

			&-2 {
				font-size: 64px;

				@include tablet() {
					font-size: 48px;
				}

				@include mobile() {
					font-size: 32px;
					line-height: 2.25;
				}
			}
		}
	}
	&-title {
		font-size: 48px;
		margin-bottom: 40px;

		@include tablet() {
			margin-bottom: 32px;
			font-size: 32px;
		}

		@include mobile() {
			font-size: 24px;
			margin-bottom: 20px;
			line-height: 2;
		}
	}

	&-service {
		@include section-padding;

		&-list {
			@include mobile() {
				row-gap: 32px;
			}
		}

		&-item {
			flex-basis: 22%;

			@include tablet() {
				flex-basis: 48%;
			}

			@include mobile() {
				flex-basis: 100%;
			}

			img {
				width: 100px;
				height: 100px;
				margin-bottom: 16px;
			}

			&-title {
				font-size: 24px;
				color: $color-secondary;
				margin-bottom: 8px;

				@include tablet() {
					font-size: 32px;
				}

				@include mobile() {
					font-size: 24px;
					line-height: 2;
				}
			}

			&-desc {
				font-size: 16px;
			}
		}
	}

	&-product {
		@include section-padding;

		&-item {
			column-gap: 30px;
			position: relative;

			@include mobile() {
				flex-direction: column;
			}

			&:not(:last-child) {
				margin-bottom: 32px;

				@include tablet() {
					margin-bottom: 8px;
				}
			}

			&:nth-child(even) {
				flex-direction: row-reverse;

				@include tablet() {
					flex-direction: row;
				}
			}

			&-img {
				flex-basis: 58.3333%;

				@include mobile() {
					flex-basis: 100%;
				}
			}

			&-category {
				@include mobile() {
					position: absolute;
					top: 50%;
					left: 50%;
					transform: translate(-50%, -50%);
				}

				&-icon {
					width: 44px;
					height: 44px;
					margin-right: 10px;
				}
			}

			&-text {
				font-size: 48px;

				@include tablet() {
					font-size: 28px;
					line-height: 2.57;
				}
			}
		}
	}

	&-mix {
		background-image: url(https://hexschool.github.io/webLayoutTraining1st/week3/bg.png);
		background-size: cover;
		@include section-padding;

		&-list {
			@include mobile() {
				gap: 16px;
			}
		}

		&-item {
			position: relative;
			flex-basis: 48%;

			@include mobile() {
				flex-basis: 100%;
			}

			&-text {
				position: absolute;
				top: 50%;
				left: 50%;
				transform: translate(-50%, -50%);
				font-size: 72px;
				white-space: nowrap;

				@include tablet() {
					font-size: 32px;
					line-height: 25px;
				}
			}
		}
	}

	&-testimonial {
		@include section-padding;

		&-list {
			@include tablet() {
				row-gap: 32px;
			}

			@include mobile() {
				row-gap: 16px;
			}
		}

		&-item {
			flex-basis: 23%;
			border: 1px solid #dee2e6;
			box-shadow: 0px 2px 6px #00000029;
			border-radius: 4px;
			overflow: hidden;
			position: relative;

			@include tablet() {
				flex-basis: 48%;
			}

			@include mobile() {
				flex-basis: 100%;
			}

			&-content {
				padding: 16px;

				@include tablet() {
					padding-left: 24px;
					padding-right: 24px;
				}
			}

			&-title {
				font-size: 14px;
				font-weight: 500;
				color: #212529;
				margin-bottom: 8px;
			}

			&-feedback {
				color: #212529;
				font-size: 16px;
				margin-bottom: 52px;

				@include tablet() {
					margin-bottom: 76px;
				}
			}

			&-img {
				width: 100%;
			}

			&-date {
				display: inline-block;
				font-size: 14px;
				color: #707070;
				position: absolute;
				bottom: 16px;
				left: 16px;

				@include tablet() {
					left: 24px;
				}
			}
		}
	}

	&-contact {
		@include section-padding;

		&-wrapper {
			width: 66.6666%;
			margin: 0 auto;

			@include mobile() {
				width: 100%;
			}
		}

		&-title {
			margin-bottom: 24px;
		}

		&-desc {
			font-size: 20px;
			margin-bottom: 20px;

			@include tablet() {
				font-size: 16px;
				margin-bottom: 16px;
			}

			@include mobile() {
				margin-bottom: 8px;
			}
		}
	}

	&-form {
		&-box {
			margin-bottom: 16px;

			@include tablet() {
				margin-bottom: 8px;
			}
		}

		&-label {
			font-size: 16px;
			margin-bottom: 4px;

			@include tablet() {
				font-size: 14px;
				margin-bottom: 3px;
			}
		}

		&-input,
		&-input-textarea {
			font-size: 16px;
			border: 1px solid #000;

			@include tablet() {
				font-size: 14px;
			}

			&:focus,
			&-textarea:focus {
				outline: none;
			}
		}

		&-input {
			&-username,
			&-phone,
			&-email,
			&-textarea {
				padding: 12px 16px;

				@include tablet() {
					padding: 8px 12px;
				}
			}

			&-textarea {
				resize: none;
				height: 136px;

				@include tablet() {
					height: 100px;
				}
			}

			&-checkbox {
				display: inline-block;
				width: 24px;
				height: 24px;
				margin-right: 4px;

				@include tablet() {
					width: 20px;
					height: 20px;
				}
			}
		}

		&-privacy {
			align-items: center;
			margin-bottom: 40px;

			@include tablet() {
				align-items: start;
				margin-bottom: 20px;
			}

			&-label {
				font-size: 16px;

				@include tablet() {
					font-size: 14px;
				}
			}
		}

		&-btn {
			border: 0;
			padding: 16px 143px;
			align-self: center;
			font-size: 16px;
			cursor: pointer;
			transition: all 0.3s;

			@include tablet() {
				padding: 10px 77px;
				font-size: 14px;
			}

			@include mobile() {
				padding: 8px 54.5px;
			}
		}
	}
}
